.combo {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    border: 1px solid white;
    cursor: pointer;
    padding: 1rem 0.5rem;
    border-radius: 0.5rem;
    gap: 0.5rem;
    width: 100%;

    &__main {
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        &__notation {
            .move-command {
                display: flex;
                box-sizing: border-box;
                border: 1px solid white;
                min-height: 2.8rem;
                padding: 0.5rem 1rem;
                border-radius: 0.5rem;
                width: fit-content;

                &.launcher {
                    border: 1px solid gold;
                    margin-bottom: 0.5rem;
                }
            }
        }

        &__damage {
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 1.4rem;
            width: 5rem;
            height: 2rem;
            padding: 0.2rem 0;
            margin-left: 0.5rem;
            font-weight: 700;
            border: 1px solid;
            border-radius: 1rem;
        }
    }


    .move-command {}

    &__note {
        font-size: 1.3rem;
        width: 100%;
        text-align: start;
    }

    &__tags {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        gap: 0.5rem;
    }
}